* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list-item {
  list-style: none;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  margin-right: 10px;
  font-size: 30px;
}

.flag {
  width: 50px;
  margin-right: 10px;
}

.country-header {
  font-size: 45px;
  font-weight: 700;
}
